import React from 'react';
import PropTypes from 'prop-types';
import ScrollToNext from '@components/ScrollToNext';
import './style.scss';
import Slide from 'react-reveal/Slide';
// import Typing from 'react-typing-animation';
import Button from '@material-ui/core/Button';
import ReactTooltip from "react-tooltip";

const AboutPage = (props, context) => {
  const {
    theme: { colorPrimary, colorHighlight, bgPrimary, textPrimary }
  } = context;

  return (
    <div className="about-page" style={{ backgroundColor: bgPrimary }}>
      <style jsx="true">
        {`
          .highlight {
            background-color: ${colorHighlight};
          }
          ::selection {
            background-color: ${colorHighlight};
          }
        `}
      </style>
      <div className="content-grid">
      <Slide right>
        <h1 style={{ color: colorPrimary }}>About</h1>
      </Slide>
        <div className="about-wrapper">
          <div className="about-content" style={{ color: textPrimary }}>
            <p>
              Actuellement chez SQLI Lyon depuis Mars 2022 pour un poste de <strong><span data-tip="Création de Contenu : Newsletter, Instagram, Facebook, Twitter, Youtube ( Marketing d'influence, Elaboration Stratégie Social Media, Veille E-Réputation) Création Graphique ( Photoshop, Illustrator, Animate, InDesign, Premiere Pro, Media Encoder, Figma)" style={{color : colorPrimary, cursor : 'pointer'}}>Concepteur</span> / <span data-tip="Developpement web & application mobile / Windows => Techno html/css, Javascript, Adobe Air, Flutter" style={{color : colorPrimary, cursor : 'pointer'}}>Développeur</span>
              
              </strong><ReactTooltip/>, suite à mon titre professionnel <strong>titre professionnel RNCP BAC +3 "Concepteur Designer UI"</strong> ainsi qu'un <strong>certificat Opquast "Maitrise de la qualité en projet web."</strong>
            </p>
            <p>
     
            </p>
            <p>
            <span style={{ textDecoration: 'underline black' }}>Les 3 principaux points : </span>
              <div  style={{ marginLeft: 50}} >
                <li>
                Conception d'un support web design et video. ( Photoshop, Illustrator, InDesign, Premiere Pro, After Effects, Figma)
                </li>
                <li>
                Gestion de Projet de communication numérique. ( Création Persona, Newsletter, Instagram, Facebook, Youtube )
                </li>
                <li>
                Evoluer la partie Front du website ( html/css, javascript, wordpress, php, flutter)
                </li>
              </div>
            </p>
            <p>
              Précédemment chez LDLC-Event Lyon durant trois ans, en tant que Front-End Developer pour les sites LDLC-OL et d4gamers
            </p>
              <p style={{ color: colorPrimary, display: 'flex', justifyContent: 'center' }}>
                <Button variant="outlined" size="medium" color="primary" target="_blank" href="https://drive.google.com/file/d/18aFzffoMMizu2Mx2U-gdWQP6xS5-bUQ2/view?usp=sharing" >
                    <strong>Titre Professionnel</strong> <span style={{ position: 'relative', left: 5, fontStyle: 'italic' }}>Animateur en Technologies de l'information et de la communcation</span>
                </Button>
              </p>
              <p style={{ color: colorPrimary, display: 'flex', justifyContent: 'center' }}>
                <Button variant="outlined" size="medium" color="primary" target="_blank" href="https://drive.google.com/file/d/1FxNHZ0bU-UAq3EPMClJbYOf1EGJ8-t_F/view?usp=share_link" >
                    <strong>Titre Professionnel</strong> <span style={{ position: 'relative', left: 5, fontStyle: 'italic' }}>Concepteur Designer UI</span>
                </Button>
              </p>
              <p  style={{ color: colorPrimary, display: 'flex', justifyContent: 'center' }}>
                <Button variant="outlined" size="medium" color="primary" target="_blank" href="https://drive.google.com/file/d/0BwFfsH91yIATWC1oU2xLSXRlclRsaFl2angtV3lmTkpBay1v/view?usp=sharing" >
                  <strong>Attestation </strong> <span style={{ position: 'relative', left: 5, fontStyle: 'italic' }}>La Capsule Coding Bootcamp</span> 
                </Button>
                <Button variant="outlined" size="medium" color="primary" target="_blank" style={{ marginLeft: '2%' }} href="https://drive.google.com/file/d/1BkJzrw672hhKtWL-XWgC-eKRa5j5zlbM/view?usp=sharing" >
                    <strong>Attestation </strong> <span style={{ position: 'relative', left: 5, fontStyle: 'italic' }}>ANSSI</span> 
                </Button>
              </p>
              <p style={{ color: colorPrimary, display: 'flex', justifyContent: 'center' }}>
                <Button variant="outlined" size="medium" color="primary" target="_blank" style={{ marginLeft: '2%' }} href="https://drive.google.com/file/d/1fU8cr9FYHjuSYyxZnfPG-QdzXn1fgokG/view?usp=sharing" >
                    <strong>Certificat </strong> <span style={{ position: 'relative', left: 5, fontStyle: 'italic' }}>OPQUAST "MAîtrise de la qualité en projet web</span> 
                </Button>
              </p>
              <p className="text-emoji" style={{ color: colorPrimary}}>
                <Button variant="outlined" size="medium" color="primary" target="_blank" href="https://drive.google.com/open?id=1siLkkpFFqINw83K7utN7OM6ADxe9TaQV" >
                  <strong>Mon CV</strong>
                </Button>
              </p>
          </div>
        </div>
      </div>
      <ScrollToNext pageSelector=".portfolio-page"/>
    </div>
  );
};

AboutPage.contextTypes = {
  theme: PropTypes.any
};

export default AboutPage;



